export default function (Component) {
      if(!Component.options.code) Component.options.code = {};
      Component.options.code['install'] = "import CommentPlugin from 'rete-comment-plugin';\n\neditor.use(CommentPlugin, { \n    margin: 20 // по умолчанию отступ для новых кадров равен 30px\n});";
    }